<template>
    <div>
        <div class="toolbar">
            <el-button @click="undo" icon="el-icon-refresh-left">撤消</el-button>
            <el-button @click="redo" icon="el-icon-refresh-right">重做</el-button>

            <el-divider direction="vertical"></el-divider>

            <el-button @click="preview(false)" icon="el-icon-view">预览</el-button>
            <el-button @click="preview(true)" icon="el-icon-picture">截图</el-button>
            <el-button @click="save" icon="el-icon-folder-add">保存</el-button>
            <el-button @click="clearCanvas" icon="el-icon-delete">清空画布</el-button>

            <el-divider direction="vertical"></el-divider>

            <div class="canvas-config">
                <span>画布大小</span>
                <input v-model="canvasStyleData.width">
                <span>*</span>
                <input v-model="canvasStyleData.height">
            </div>
        </div>

        <!-- 预览 -->
        <Preview v-if="isShowPreview" :is-screenshot="isScreenshot" @close="handlePreviewChange" />
    </div>
</template>

<script>
import { mapState } from 'vuex'
import Preview from '@/components/Editor/Preview'
import eventBus from '@/utils/eventBus'
import { $ } from '@/utils/utils'
import { storeCanvasData, storeCanvasStyle } from '@/utils/dataApi'

export default {
    components: { Preview },
    data() {
        return {
            scale: 100,
            isShowPreview: false,
            isScreenshot: false,
        }
    },
    computed: mapState([
        'componentData',
        'canvasStyleData',
        'curComponent',
        'curComponentIndex',
    ]),
    created() {
        eventBus.$on('preview', this.preview)
        eventBus.$on('save', this.save)
        eventBus.$on('clearCanvas', this.clearCanvas)

        this.scale = this.canvasStyleData.scale
    },
    methods: {
        redo() {
            this.$store.commit('redo')
        },
        undo() {
            this.$store.commit('undo')
        },
        
        preview(isScreenshot) {
            this.isScreenshot = isScreenshot
            this.isShowPreview = true
            this.$store.commit('setEditMode', 'preview')
        },

        handlePreviewChange() {
            this.isShowPreview = false
            this.$store.commit('setEditMode', 'edit')
        },

        save() {
            storeCanvasData(JSON.stringify(this.componentData))
            storeCanvasStyle(JSON.stringify(this.canvasStyleData))

            this.$message.success('保存成功')
        },

        clearCanvas() {
            this.$store.commit('setCurComponent', { component: null, index: null })
            this.$store.commit('setComponentData', [])
            this.$store.commit('recordSnapshot')
        },
    },
}
</script>

