export function getCanvasData() {
    return localStorage.getItem('canvasData')
}

export function getCanvasStyle() {
    return localStorage.getItem('canvasStyle')
}

export function storeCanvasData(json) {
    return localStorage.setItem('canvasData', json)
}

export function storeCanvasStyle(json) {
    return localStorage.setItem('canvasStyle', json)
}